import * as R from 'ramda'
import React from 'react'
import styled from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'

const MARGIN = '50px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

const Item = styled('div')`
  display: flex;
  align-items: center;
`

const Name = styled('div')`
  border: 1px dashed white;
  padding: 20px;
`

const Licence = styled('div')`
  border: 1px dashed white;
  border-left: 0;
  padding: 20px;
`

export const Licences = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      {data &&
        data.length > 0 &&
        data.map(item => {
          const id = R.prop('id', item)
          const version = R.prop('version', item)
          const licences = R.pipe(
            R.path(['licenseSources', 'package', 'sources']),
            R.map(R.prop('licence')),
          )

          console.log(licences(item))

          return (
            <Item>
              <Name>
                {id}@{version}
              </Name>
              <Licence>MIT</Licence>
            </Item>
          )
        })}
    </Wrapper>
  )
}



